<template>
  <div id="record-banner-page">
    <header>
      <img src="../../assets/imgs/images/pre.png" @click="goback" />
      <p>读本合集</p>
    </header>
    <div class="content-title">
      <div class="pic-box">
        <img :src="bgcPicData.banner_img_url" />
        <div class="text-box">
          <h3>{{ bgcPicData.album_name }}</h3>
          <p>
            {{ bgcPicData.remark }}
          </p>
        </div>
      </div>
    </div>
    <div class="items">
      <RecordSection :listData="listData"></RecordSection>
    </div>
  </div>
</template>

<script>
import RecordSection from "@/components/RecordSection.vue";
export default {
  components: { RecordSection },
  name: "recordBannerPage",
  data() {
    return {
      bgcPicData: "",
      listData: [],
    };
  },
  created() {
    this.$nextTick(() => {
      this.getBgcPicData();
    });
  },
  methods: {
    goback() {
      this.$router.back(-1);
    },
    async getBgcPicData() {
      let data = await this.$axios({
        method: "post",
        url: "https://app.thepoemforyou.com/api/v1/getBookAlbumInfo",
        data: {
          id: this.$route.query.id,
        },
      });
      this.bgcPicData = data.data.data;
      let data1 = await this.$axios({
        method: "post",
        url: "https://app.thepoemforyou.com/api/v1/getBookList",
        data: {
          list: this.bgcPicData.list,
        },
      });
      this.listData = data1.data.data;
    },
  },
};
</script>

<style lang="scss" scoped>
#record-banner-page {
  header {
    position: sticky;
    top: 0;
    left: 0;
    padding: 10px 20px;
    height: 50px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    background-color: #fff;
    img {
      width: 10px;
    }
    p {
      width: 100%;
      font-size: 15px;
      text-align: center;
    }
  }
  .content-title {
    margin-top: 20px;
    width: 100%;
    .pic-box {
      position: relative;
      img {
        width: 100%;
      }
      .text-box {
        position: absolute;
        top: 120px;
        width: 91%;
        padding: 20px 15px;
        margin: 0 15px;
        background-color: #fff;
        box-shadow: 0 0 8px var(--theme-shadow);
        h3 {
          font-size: 15px;
          font-weight: 500;
          margin-top: 5px;
          margin-bottom: 15px;
        }
        p {
          font-size: 14px;
          line-height: 1.7em;
          color: var(--theme-gray);
        }
      }
    }
  }
  .items {
    margin-top: 130px;
  }
}
</style>